@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
:root{
    --gap: 5px;
}

body{
    background-color: #00537a;
    background-image: url("https://www.transparenttextures.com/patterns/food.png");
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

::-webkit-scrollbar{
    width: 10px;
    height: 10px;
}
::-webkit-scrollbar-thumb{
    background-color: rgba(189, 189, 189, 0.8);
    border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover{
    background-color: rgba(189, 189, 189, 1);
}
::-webkit-scrollbar-track{
    background-color: transparent;
}
.outer-container{
}

#title{
    font-family: 'Gloria Hallelujah', cursive;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.title-span{
    position: relative;
    display: inline-block;
    /* animation: rotate 0.5s linear infinite alternate; */
}
.title-span::after{
    content: attr(data-content);
    position: absolute;
    left: 5px;
    z-index: -1;
    color: black;
    -webkit-animation: shake 0.25s steps(2) infinite alternate forwards;
            animation: shake 0.25s steps(2) infinite alternate forwards;
}

.form-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#login-form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 1rem;
    border-radius: 5px;
    background-color: white;
}

#login-form input{
    padding: 1rem;
    font-size: 15px;
    outline: none;
    border: 1px solid gray;
    border-radius: 5px;
}

#login-form input:focus{
    border: 1px solid #00537a;
}

#play{
    background-color: #00537a;
    border:none;
    border-radius: 5px;
    padding: 1rem;
    color: white;
    cursor: pointer;
}

.mt-10{
    margin-top: 10px;
}

#error{
    color: red;
}

@-webkit-keyframes rotate {
    0%{
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    100%{
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }
}

@keyframes rotate {
    0%{
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }
    100%{
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }
}

@-webkit-keyframes shake {
    0%{
        left: -5px;
    }
    100%{
        left: 5px;
    }
}

@keyframes shake {
    0%{
        left: -5px;
    }
    100%{
        left: 5px;
    }
}
nav{
    position: -webkit-sticky;
    position: sticky;
    left: 0;
}

.outer-container{
    overflow-y: hidden;
    min-height: 100vh;
    margin-bottom: 10px;
}

.center-wait{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.wait{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
}

.word-bar{
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    background: rgba(255, 255, 255, 0.8);
    text-align: center;
    border-radius: 5px;
    width: 100%;
    margin: 10px auto;
}
.word-bar h1{
    display: inline-block;
    margin: 0;
    padding: 10px;
    font-size: 1.5em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.word{
    position: relative;
}
.timer{
    position: absolute;
    left: 0;
}

.canvas-outer-container{
    overflow-x: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 90vh;
}

.canvas-container{
    min-width: 400px;
    width: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.word-options-outer-container{
    background: white;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.word-options-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin: 15px;
    border-radius: 5px;
}

.word-option{
    background: rgba(1,1,1,0.5);
    border: none;
    margin: 15px;
    padding: 10px;
    border-radius: 5px;
    color: white;
}
.word-option:hover{
    background: rgba(1,1,1,0.7);
    cursor: pointer;
}

.scores-container{
    min-width: 200px;
    width: 25vw;
    margin: 0;
    padding: 0;
    margin-right: 10px;
    overflow-y: auto;
    border-radius: 5px;
}
.card-container{
    width: 100%;
    margin-bottom: 2px;
    background: white;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.card-container h3{
    margin:0;
    padding: 5px;
}

#user-name{
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-right: 5px;
    color: rgb(78, 78, 78);
}

#user-score{
    color: green;
}
.guess-container{
    min-width: 200px;
    width: 25vw;
    background-color: white;
    margin-left: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-radius: 5px;
}

.guess-list{
    height: 100%;
    overflow-y: auto;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.input-container{
    height: 2.5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 5px;
}

.guess-input{
    width: 100%;
    outline: none;
    padding: 0;
    padding-left: 10px;
    height: 2rem;
    margin: 5px;
    border-radius: 5px;
    border: 1px solid gray;
}
.score-board{
    overflow-y: auto;
    background-color: white;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
}
.scores{
    text-align: center;
}
.score-card{
    display: -webkit-flex;
    display: flex;
    background-color: rgba(0, 0, 0, 0.1);
    margin: 10px auto;
    border-radius: 10px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.score-card h1{
    margin: 10px;
    font-weight: 200;
}
.mr-20{
    margin-right: 20px;
}

.ml-20{
    margin-left: 20px;
}

#canvas{
    /* cursor: url('./Cursor/edit5.svg') 5 5, auto; */
    border-radius: 5px;
}

.options-outer-container{
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    overflow-x: auto;
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

.options-container{
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 5px;
    padding: 5px;
    margin: auto 0.5em;
}
.option-icon{
    border: 1px solid black;
    border-radius: 50%;
    padding: 0.5em;
    margin: 0.2em;
    cursor: pointer;
}
.option-icon:hover{
    background-color: black;
    color: white;
}
.option-icon-active{
    background-color: black;
    color: white;
}
.color-box{
    width: 1.2em;
    height: 1.2em;
    margin: 2px;
    border: 1px solid black;
    display: inline-block;
    cursor: pointer;
    transition: 0.2s ease;
}
.color-box:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.2s ease;
}
.color-box-active{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.2s ease;
}
.color-box-active::after{
    content: '✔️';
    font-size: 0.7em;
    position: absolute;
    left: 0;
    top: 0;
}

@media screen and (max-width : 650px){
    .options-outer-container{
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
    }
}
